<template>
  <div class="step-result">
    <div class="text-center mb-3">
      <button class="btn-custom mr-3" @click="saveResult">   </button>
      <button class="btn-custom" @click="$router.push('/')">   </button>
    </div>
    <div class="mb-5">
      <div
        ref="result"
        :class="`m-auto outter-frame outter-frame-${columns}-${rows}`"
      >
        <result-frame :rows="rows" :columns="columns" />
      </div>
    </div>
  </div>
</template>

<script>
import resultFrame from './frames/resultFrame.vue';
import html2canvas from 'html2canvas';

export default {
  name: 'step-result',
  components: { resultFrame },
  data() {
    return {
      rows: 2,
      columns: 1,
    };
  },
  created() {
    const table = this.$store.getters.getTable;
    this.rows    = table.rows;
    this.columns = table.columns;
  },
  methods: {
    async saveResult() {
      const el   = this.$refs.result;
      const cssW = el.offsetWidth;    // 1080
      const cssH = el.offsetHeight;   // 1620
      const scale = 3;                //   ( 2)

      // html2canvas:    cssW*scale  cssH*scale  
      const hiResCanvas = await html2canvas(el, {
        backgroundColor: '#FFF',
        scale,
        width: cssW,
        height: cssH,
        useCORS: true
      });

      // 
      const dataURL = hiResCanvas.toDataURL('image/jpeg', 0.9);
      const a = document.createElement('a');
      a.href    = dataURL;
      a.download = 'highres-capture.jpg';
      a.click();
    }
  }
};
</script>

<style lang="scss" scoped>
.outter-frame {
  /*   : 10801620px  */
  width:  1080px;
  height: 1620px;
  overflow: hidden;
}
</style>
